'use client'

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import GraficoProgreso from '@/components/GraficoProgreso'
import LineaTiempoProgreso from '@/components/LineaTiempoProgreso'

interface RegistroProgreso {
  id: string
  fecha: string
  peso: number
  medidas: {
    pecho: number
    cintura: number
    brazo: number
    muslo: number
  }
  fotos?: string[]
  notas?: string
}

export default function PaginaProgreso() {
  const [registros, setRegistros] = useState<RegistroProgreso[]>([])
  const [mostrarFormulario, setMostrarFormulario] = useState(false)
  const [nuevoRegistro, setNuevoRegistro] = useState({
    peso: '',
    pecho: '',
    cintura: '',
    brazo: '',
    muslo: '',
    notas: ''
  })

  useEffect(() => {
    cargarRegistros()
  }, [])

  const cargarRegistros = () => {
    try {
      const registrosGuardados = localStorage.getItem('registrosProgreso')
      if (registrosGuardados) {
        setRegistros(JSON.parse(registrosGuardados))
      }
    } catch (error) {
      console.error('Error cargando registros:', error)
    }
  }

  const guardarRegistro = () => {
    if (!nuevoRegistro.peso) {
      alert('Por favor, ingresa al menos el peso')
      return
    }

    const registro: RegistroProgreso = {
      id: Date.now().toString(),
      fecha: new Date().toISOString(),
      peso: parseFloat(nuevoRegistro.peso),
      medidas: {
        pecho: parseFloat(nuevoRegistro.pecho) || 0,
        cintura: parseFloat(nuevoRegistro.cintura) || 0,
        brazo: parseFloat(nuevoRegistro.brazo) || 0,
        muslo: parseFloat(nuevoRegistro.muslo) || 0
      },
      notas: nuevoRegistro.notas
    }

    const registrosActualizados = [...registros, registro]
    setRegistros(registrosActualizados)
    localStorage.setItem('registrosProgreso', JSON.stringify(registrosActualizados))
    
    // Limpiar formulario
    setNuevoRegistro({
      peso: '',
      pecho: '',
      cintura: '',
      brazo: '',
      muslo: '',
      notas: ''
    })
    
    setMostrarFormulario(false)
    alert('Registro guardado exitosamente!')
  }

  const eliminarRegistro = (id: string) => {
    if (confirm('¿Estás seguro de que quieres eliminar este registro?')) {
      const registrosActualizados = registros.filter(r => r.id !== id)
      setRegistros(registrosActualizados)
      localStorage.setItem('registrosProgreso', JSON.stringify(registrosActualizados))
    }
  }

  // Preparar datos para gráficos
  const datosPeso = registros.map(r => ({
    fecha: new Date(r.fecha).toLocaleDateString('es-ES', { day: '2-digit', month: '2-digit' }),
    valor: r.peso
  }))

  const datosPecho = registros.filter(r => r.medidas.pecho > 0).map(r => ({
    fecha: new Date(r.fecha).toLocaleDateString('es-ES', { day: '2-digit', month: '2-digit' }),
    valor: r.medidas.pecho
  }))

  const ultimoRegistro = registros[registros.length - 1]
  const primerRegistro = registros[0]

  return (
    <div className="space-y-6">
      {/* Encabezado */}
      <div className="flex flex-col md:flex-row md:items-center md:justify-between">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Seguimiento de Progreso</h1>
          <p className="text-gray-600 mt-1">
            Registra y visualiza tu evolución física
          </p>
        </div>
        <Button onClick={() => setMostrarFormulario(true)} className="mt-4 md:mt-0">
          Nuevo Registro
        </Button>
      </div>

      {/* Resumen rápido */}
      {ultimoRegistro && (
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
          <Card>
            <CardContent className="p-4 text-center">
              <div className="text-2xl font-bold text-blue-600">{ultimoRegistro.peso} kg</div>
              <div className="text-sm text-gray-600">Peso Actual</div>
              {primerRegistro && primerRegistro.id !== ultimoRegistro.id && (
                <div className="text-xs text-gray-500">
                  {ultimoRegistro.peso > primerRegistro.peso ? '+' : ''}
                  {(ultimoRegistro.peso - primerRegistro.peso).toFixed(1)} kg
                </div>
              )}
            </CardContent>
          </Card>
          
          <Card>
            <CardContent className="p-4 text-center">
              <div className="text-2xl font-bold text-green-600">{ultimoRegistro.medidas.pecho || '-'}</div>
              <div className="text-sm text-gray-600">Pecho (cm)</div>
            </CardContent>
          </Card>
          
          <Card>
            <CardContent className="p-4 text-center">
              <div className="text-2xl font-bold text-purple-600">{ultimoRegistro.medidas.brazo || '-'}</div>
              <div className="text-sm text-gray-600">Brazo (cm)</div>
            </CardContent>
          </Card>
          
          <Card>
            <CardContent className="p-4 text-center">
              <div className="text-2xl font-bold text-orange-600">{registros.length}</div>
              <div className="text-sm text-gray-600">Registros</div>
            </CardContent>
          </Card>
        </div>
      )}

      <Tabs defaultValue="graficos" className="space-y-6">
        <TabsList>
          <TabsTrigger value="graficos">Gráficos</TabsTrigger>
          <TabsTrigger value="registros">Registros</TabsTrigger>
          <TabsTrigger value="fotos">Fotos</TabsTrigger>
        </TabsList>

        <TabsContent value="graficos" className="space-y-6">
          {datosPeso.length > 0 ? (
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
              <GraficoProgreso
                titulo="Evolución del Peso"
                descripcion="Últimos registros"
                datos={datosPeso}
                tipo="linea"
                color="#3b82f6"
                unidad=" kg"
              />
              
              {datosPecho.length > 0 && (
                <GraficoProgreso
                  titulo="Medida del Pecho"
                  descripcion="Últimos registros"
                  datos={datosPecho}
                  tipo="linea"
                  color="#10b981"
                  unidad=" cm"
                />
              )}
            </div>
          ) : (
            <Card>
              <CardContent className="text-center py-12">
                <div className="text-6xl mb-4">📈</div>
                <h3 className="text-xl font-semibold mb-2">Sin datos para mostrar</h3>
                <p className="text-gray-600 mb-6">
                  Registra tu primer progreso para ver los gráficos
                </p>
                <Button onClick={() => setMostrarFormulario(true)}>
                  Crear Primer Registro
                </Button>
              </CardContent>
            </Card>
          )}
        </TabsContent>

        <TabsContent value="registros" className="space-y-6">
          {registros.length > 0 ? (
            <div className="space-y-4">
              {registros.slice().reverse().map((registro) => (
                <Card key={registro.id}>
                  <CardHeader>
                    <div className="flex items-center justify-between">
                      <CardTitle className="text-lg">
                        {new Date(registro.fecha).toLocaleDateString('es-ES', {
                          weekday: 'long',
                          year: 'numeric',
                          month: 'long',
                          day: 'numeric'
                        })}
                      </CardTitle>
                      <Button
                        variant="destructive"
                        size="sm"
                        onClick={() => eliminarRegistro(registro.id)}
                      >
                        Eliminar
                      </Button>
                    </div>
                  </CardHeader>
                  <CardContent>
                    <div className="grid grid-cols-2 md:grid-cols-5 gap-4">
                      <div>
                        <div className="text-sm text-gray-600">Peso</div>
                        <div className="font-semibold">{registro.peso} kg</div>
                      </div>
                      <div>
                        <div className="text-sm text-gray-600">Pecho</div>
                        <div className="font-semibold">{registro.medidas.pecho || '-'} cm</div>
                      </div>
                      <div>
                        <div className="text-sm text-gray-600">Cintura</div>
                        <div className="font-semibold">{registro.medidas.cintura || '-'} cm</div>
                      </div>
                      <div>
                        <div className="text-sm text-gray-600">Brazo</div>
                        <div className="font-semibold">{registro.medidas.brazo || '-'} cm</div>
                      </div>
                      <div>
                        <div className="text-sm text-gray-600">Muslo</div>
                        <div className="font-semibold">{registro.medidas.muslo || '-'} cm</div>
                      </div>
                    </div>
                    {registro.notas && (
                      <div className="mt-3">
                        <div className="text-sm text-gray-600">Notas</div>
                        <div className="text-sm">{registro.notas}</div>
                      </div>
                    )}
                  </CardContent>
                </Card>
              ))}
            </div>
          ) : (
            <Card>
              <CardContent className="text-center py-12">
                <div className="text-6xl mb-4">📝</div>
                <h3 className="text-xl font-semibold mb-2">Sin registros</h3>
                <p className="text-gray-600 mb-6">
                  Comienza a registrar tu progreso físico
                </p>
                <Button onClick={() => setMostrarFormulario(true)}>
                  Primer Registro
                </Button>
              </CardContent>
            </Card>
          )}
        </TabsContent>

        <TabsContent value="fotos">
          <LineaTiempoProgreso registros={registros} />
        </TabsContent>
      </Tabs>

      {/* Modal de nuevo registro */}
      {mostrarFormulario && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
          <Card className="w-full max-w-md">
            <CardHeader>
              <CardTitle>Nuevo Registro de Progreso</CardTitle>
              <CardDescription>
                Registra tu peso y medidas corporales
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div>
                <Label htmlFor="peso">Peso (kg) *</Label>
                <Input
                  id="peso"
                  type="number"
                  step="0.1"
                  value={nuevoRegistro.peso}
                  onChange={(e) => setNuevoRegistro({...nuevoRegistro, peso: e.target.value})}
                  placeholder="70.5"
                />
              </div>
              
              <div className="grid grid-cols-2 gap-3">
                <div>
                  <Label htmlFor="pecho">Pecho (cm)</Label>
                  <Input
                    id="pecho"
                    type="number"
                    value={nuevoRegistro.pecho}
                    onChange={(e) => setNuevoRegistro({...nuevoRegistro, pecho: e.target.value})}
                    placeholder="100"
                  />
                </div>
                <div>
                  <Label htmlFor="cintura">Cintura (cm)</Label>
                  <Input
                    id="cintura"
                    type="number"
                    value={nuevoRegistro.cintura}
                    onChange={(e) => setNuevoRegistro({...nuevoRegistro, cintura: e.target.value})}
                    placeholder="80"
                  />
                </div>
              </div>
              
              <div className="grid grid-cols-2 gap-3">
                <div>
                  <Label htmlFor="brazo">Brazo (cm)</Label>
                  <Input
                    id="brazo"
                    type="number"
                    value={nuevoRegistro.brazo}
                    onChange={(e) => setNuevoRegistro({...nuevoRegistro, brazo: e.target.value})}
                    placeholder="35"
                  />
                </div>
                <div>
                  <Label htmlFor="muslo">Muslo (cm)</Label>
                  <Input
                    id="muslo"
                    type="number"
                    value={nuevoRegistro.muslo}
                    onChange={(e) => setNuevoRegistro({...nuevoRegistro, muslo: e.target.value})}
                    placeholder="55"
                  />
                </div>
              </div>
              
              <div>
                <Label htmlFor="notas">Notas (opcional)</Label>
                <Input
                  id="notas"
                  value={nuevoRegistro.notas}
                  onChange={(e) => setNuevoRegistro({...nuevoRegistro, notas: e.target.value})}
                  placeholder="Cómo te sientes, cambios notados..."
                />
              </div>
              
              <div className="flex gap-3 pt-4">
                <Button variant="outline" onClick={() => setMostrarFormulario(false)} className="flex-1">
                  Cancelar
                </Button>
                <Button onClick={guardarRegistro} className="flex-1">
                  Guardar
                </Button>
              </div>
            </CardContent>
          </Card>
        </div>
      )}
    </div>
  )
}
