'use client'

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { ChartConfig, ChartContainer, ChartTooltip, ChartTooltipContent } from '@/components/ui/chart'
import { LineChart, Line, XAxis, YAxis, CartesianGrid, ResponsiveContainer, BarChart, Bar } from 'recharts'

interface DatosGrafico {
  fecha: string
  valor: number
  etiqueta?: string
}

interface GraficoProgresoProps {
  titulo: string
  descripcion?: string
  datos: DatosGrafico[]
  tipo: 'linea' | 'barra'
  color?: string
  unidad?: string
  className?: string
}

const chartConfig = {
  valor: {
    label: "Valor",
    color: "hsl(var(--chart-1))",
  },
} satisfies ChartConfig

export default function GraficoProgreso({
  titulo,
  descripcion,
  datos,
  tipo = 'linea',
  color = '#000000',
  unidad = '',
  className
}: GraficoProgresoProps) {
  const formatearValor = (valor: number) => {
    return `${valor}${unidad}`
  }

  const renderGrafico = () => {
    if (tipo === 'barra') {
      return (
        <BarChart data={datos}>
          <CartesianGrid strokeDasharray="3 3" stroke="#f0f0f0" />
          <XAxis 
            dataKey="fecha" 
            stroke="#666"
            fontSize={12}
            tickLine={false}
            axisLine={false}
          />
          <YAxis 
            stroke="#666"
            fontSize={12}
            tickLine={false}
            axisLine={false}
            tickFormatter={formatearValor}
          />
          <ChartTooltip
            cursor={false}
            content={<ChartTooltipContent />}
          />
          <Bar 
            dataKey="valor" 
            fill={color}
            radius={[4, 4, 0, 0]}
          />
        </BarChart>
      )
    }

    return (
      <LineChart data={datos}>
        <CartesianGrid strokeDasharray="3 3" stroke="#f0f0f0" />
        <XAxis 
          dataKey="fecha" 
          stroke="#666"
          fontSize={12}
          tickLine={false}
          axisLine={false}
        />
        <YAxis 
          stroke="#666"
          fontSize={12}
          tickLine={false}
          axisLine={false}
          tickFormatter={formatearValor}
        />
        <ChartTooltip
          cursor={false}
          content={<ChartTooltipContent />}
        />
        <Line 
          type="monotone" 
          dataKey="valor" 
          stroke={color}
          strokeWidth={3}
          dot={{ fill: color, strokeWidth: 2, r: 4 }}
          activeDot={{ r: 6, stroke: color, strokeWidth: 2 }}
        />
      </LineChart>
    )
  }

  return (
    <Card className={className}>
      <CardHeader>
        <CardTitle className="text-lg font-semibold">{titulo}</CardTitle>
        {descripcion && (
          <CardDescription>{descripcion}</CardDescription>
        )}
      </CardHeader>
      <CardContent>
        {datos.length > 0 ? (
          <ChartContainer config={chartConfig} className="h-[300px] w-full">
            {renderGrafico()}
          </ChartContainer>
        ) : (
          <div className="h-[300px] flex items-center justify-center text-gray-500">
            <div className="text-center">
              <div className="text-4xl mb-2">📊</div>
              <p>No hay datos disponibles</p>
              <p className="text-sm">Comienza a registrar tu progreso</p>
            </div>
          </div>
        )}
      </CardContent>
    </Card>
  )
}
