'use client'

import { useState } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'

interface RegistroProgreso {
  id: string
  fecha: string
  peso: number
  medidas: {
    pecho: number
    cintura: number
    brazo: number
    muslo: number
  }
  fotos?: string[]
  notas?: string
}

interface LineaTiempoProgresoProps {
  registros: RegistroProgreso[]
}

export default function LineaTiempoProgreso({ registros }: LineaTiempoProgresoProps) {
  const [fotoSeleccionada, setFotoSeleccionada] = useState<File | null>(null)
  const [subiendoFoto, setSubiendoFoto] = useState(false)

  const manejarSeleccionFoto = (event: React.ChangeEvent<HTMLInputElement>) => {
    const archivo = event.target.files?.[0]
    if (archivo) {
      if (archivo.size > 5 * 1024 * 1024) { // 5MB límite
        alert('La imagen es demasiado grande. Por favor selecciona una imagen menor a 5MB.')
        return
      }
      
      if (!archivo.type.startsWith('image/')) {
        alert('Por favor selecciona un archivo de imagen válido.')
        return
      }
      
      setFotoSeleccionada(archivo)
      subirFoto(archivo)
    }
  }

  const subirFoto = async (archivo: File) => {
    setSubiendoFoto(true)
    try {
      // Convertir a base64 para almacenamiento local
      const reader = new FileReader()
      reader.onload = (e) => {
        const base64 = e.target?.result as string
        
        // Crear nuevo registro de progreso con foto
        const nuevoRegistro = {
          id: Date.now().toString(),
          fecha: new Date().toISOString(),
          peso: 0, // Se puede actualizar después
          medidas: { pecho: 0, cintura: 0, brazo: 0, muslo: 0 },
          fotos: [base64],
          notas: 'Foto de progreso'
        }
        
        // Guardar en localStorage
        const registrosExistentes = JSON.parse(localStorage.getItem('registrosProgreso') || '[]')
        const registrosActualizados = [...registrosExistentes, nuevoRegistro]
        localStorage.setItem('registrosProgreso', JSON.stringify(registrosActualizados))
        
        alert('¡Foto subida exitosamente!')
        // Recargar la página para mostrar la nueva foto
        window.location.reload()
      }
      reader.readAsDataURL(archivo)
    } catch (error) {
      console.error('Error subiendo foto:', error)
      alert('Error al subir la foto. Inténtalo de nuevo.')
    } finally {
      setSubiendoFoto(false)
      setFotoSeleccionada(null)
    }
  }

  if (registros.length === 0) {
    return (
      <Card>
        <CardContent className="text-center py-12">
          <div className="text-6xl mb-4">📸</div>
          <h3 className="text-xl font-semibold mb-2">Galería de Progreso</h3>
          <p className="text-gray-600 mb-6">
            Sube tu primera foto para comenzar a documentar tu progreso visual
          </p>
          <div className="space-y-4">
            <input
              type="file"
              accept="image/*"
              onChange={manejarSeleccionFoto}
              className="hidden"
              id="foto-input"
            />
            <Button 
              asChild
              disabled={subiendoFoto}
              className="cursor-pointer"
            >
              <label htmlFor="foto-input">
                {subiendoFoto ? 'Subiendo...' : '📷 Subir Primera Foto'}
              </label>
            </Button>
            <p className="text-xs text-gray-500">
              Formatos soportados: JPG, PNG, GIF (máx. 5MB)
            </p>
          </div>
        </CardContent>
      </Card>
    )
  }

  return (
    <div className="space-y-6">
      {/* Botón para subir nueva foto */}
      <Card>
        <CardHeader>
          <CardTitle>Subir Nueva Foto</CardTitle>
          <CardDescription>
            Documenta tu progreso con una nueva foto
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="flex items-center gap-4">
            <input
              type="file"
              accept="image/*"
              onChange={manejarSeleccionFoto}
              className="hidden"
              id="nueva-foto-input"
            />
            <Button 
              asChild
              disabled={subiendoFoto}
              className="cursor-pointer"
            >
              <label htmlFor="nueva-foto-input">
                {subiendoFoto ? 'Subiendo...' : '📷 Subir Nueva Foto'}
              </label>
            </Button>
            <p className="text-sm text-gray-500">
              JPG, PNG, GIF (máx. 5MB)
            </p>
          </div>
        </CardContent>
      </Card>

      <Card>
        <CardHeader>
          <CardTitle>Línea de Tiempo Visual</CardTitle>
          <CardDescription>
            Tu progreso documentado con fotos y medidas a lo largo del tiempo
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="space-y-6">
            {registros.slice().reverse().map((registro, index) => (
              <div key={registro.id} className="flex gap-4">
                {/* Línea de tiempo */}
                <div className="flex flex-col items-center">
                  <div className="w-4 h-4 bg-black rounded-full"></div>
                  {index < registros.length - 1 && (
                    <div className="w-0.5 h-16 bg-gray-300 mt-2"></div>
                  )}
                </div>

                {/* Contenido */}
                <div className="flex-1">
                  <Card>
                    <CardHeader>
                      <div className="flex items-center justify-between">
                        <CardTitle className="text-lg">
                          {new Date(registro.fecha).toLocaleDateString('es-ES', {
                            weekday: 'long',
                            year: 'numeric',
                            month: 'long',
                            day: 'numeric'
                          })}
                        </CardTitle>
                        <div className="text-sm text-gray-600">
                          {registro.peso > 0 ? `${registro.peso} kg` : 'Sin peso registrado'}
                        </div>
                      </div>
                    </CardHeader>
                    <CardContent>
                      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                        {/* Foto */}
                        <div className="aspect-square bg-gray-100 rounded-lg flex items-center justify-center overflow-hidden">
                          {registro.fotos && registro.fotos.length > 0 ? (
                            <img 
                              src={registro.fotos[0]}
                              alt={`Foto de progreso del ${new Date(registro.fecha).toLocaleDateString('es-ES')}`}
                              className="w-full h-full object-cover rounded-lg"
                              onError={(e) => {
                                const target = e.target as HTMLImageElement
                                target.style.display = 'none'
                                const parent = target.parentElement
                                if (parent) {
                                  parent.innerHTML = `
                                    <div class="text-center text-gray-500">
                                      <div class="text-4xl mb-2">📷</div>
                                      <div class="text-sm">Error cargando foto</div>
                                    </div>
                                  `
                                }
                              }}
                            />
                          ) : (
                            <img 
                              src={`https://placehold.co/400x400?text=Progreso+${new Date(registro.fecha).toLocaleDateString('es-ES', { day: '2-digit', month: '2-digit' })}`}
                              alt={`Placeholder de progreso del ${new Date(registro.fecha).toLocaleDateString('es-ES')}`}
                              className="w-full h-full object-cover rounded-lg"
                              onError={(e) => {
                                const target = e.target as HTMLImageElement
                                target.style.display = 'none'
                                const parent = target.parentElement
                                if (parent) {
                                  parent.innerHTML = `
                                    <div class="text-center text-gray-500">
                                      <div class="text-4xl mb-2">📷</div>
                                      <div class="text-sm">Foto no disponible</div>
                                    </div>
                                  `
                                }
                              }}
                            />
                          )}
                        </div>

                        {/* Medidas */}
                        <div className="space-y-4">
                          <h4 className="font-semibold">Medidas Corporales</h4>
                          <div className="grid grid-cols-2 gap-3">
                            <div className="bg-blue-50 p-3 rounded-lg">
                              <div className="text-sm text-gray-600">Pecho</div>
                              <div className="text-lg font-semibold text-blue-600">
                                {registro.medidas.pecho || '-'} cm
                              </div>
                            </div>
                            <div className="bg-green-50 p-3 rounded-lg">
                              <div className="text-sm text-gray-600">Cintura</div>
                              <div className="text-lg font-semibold text-green-600">
                                {registro.medidas.cintura || '-'} cm
                              </div>
                            </div>
                            <div className="bg-purple-50 p-3 rounded-lg">
                              <div className="text-sm text-gray-600">Brazo</div>
                              <div className="text-lg font-semibold text-purple-600">
                                {registro.medidas.brazo || '-'} cm
                              </div>
                            </div>
                            <div className="bg-orange-50 p-3 rounded-lg">
                              <div className="text-sm text-gray-600">Muslo</div>
                              <div className="text-lg font-semibold text-orange-600">
                                {registro.medidas.muslo || '-'} cm
                              </div>
                            </div>
                          </div>

                          {registro.notas && (
                            <div className="bg-gray-50 p-3 rounded-lg">
                              <div className="text-sm text-gray-600 mb-1">Notas</div>
                              <div className="text-sm">{registro.notas}</div>
                            </div>
                          )}
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                </div>
              </div>
            ))}
          </div>
        </CardContent>
      </Card>

      {/* Funcionalidad futura */}
      <Card>
        <CardHeader>
          <CardTitle>Próximamente: Comparación Visual</CardTitle>
          <CardDescription>
            Funcionalidades que estarán disponibles próximamente
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <div className="text-center p-4 border rounded-lg">
              <div className="text-3xl mb-2">🔄</div>
              <h4 className="font-semibold mb-1">Comparar Fechas</h4>
              <p className="text-sm text-gray-600">Compara fotos lado a lado</p>
            </div>
            <div className="text-center p-4 border rounded-lg">
              <div className="text-3xl mb-2">📊</div>
              <h4 className="font-semibold mb-1">Análisis IA</h4>
              <p className="text-sm text-gray-600">Análisis automático de cambios</p>
            </div>
            <div className="text-center p-4 border rounded-lg">
              <div className="text-3xl mb-2">📱</div>
              <h4 className="font-semibold mb-1">Múltiples Fotos</h4>
              <p className="text-sm text-gray-600">Subir varias fotos por fecha</p>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
